import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, Row, Col, Divider, DatePicker } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/profitLossComparativeReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const xx = 15;


class ProfitLossReport extends Component {
  state = {
    filter: {
      first_start_date: moment().startOf('month').subtract('seconds', 1).startOf('month').format("YYYY-MM-DD HH:mm:ss"),
      first_end_date: moment().startOf('month').subtract('seconds', 1).format("YYYY-MM-DD HH:mm:ss"),
      second_start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
      second_end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getProfitLossComparativeReports } = this.props;
    getProfitLossComparativeReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.profitLossComparativeReport.error) {
      notification("error", this.props.profitLossComparativeReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.profitLossComparativeReport.success) {
      notification("success", this.props.profitLossComparativeReport.success);
      this.props.clearSuccess();
    }
  }

  // Fungsi marakke mumet ndase 
  hitungTotalAmountParent = (array_ori, array_filter) => {
    //console.log(array_filter)
    array_filter.map((data) => {
      if (data.has_child === 1) {
        let filter = array_ori.filter((x) => x.id_account_parent === data.id_account)
        if (filter.length > 0) {
          this.hitungTotalAmountParent(array_ori, filter)
        }
      }
      else if (data.id_account_parent === null) {
        console.log("Ya")
      }
      else {

        let parent = array_ori.find((x) => x.id_account === data.id_account_parent)
        array_ori.map((x) => {

          if (x.id_account === parent.id_account) {
            let parent2 = parent
            while (parent2 !== undefined) {
              parent2.previous_amount += parseFloat(data.previous_amount)
			        parent2.current_amount += parseFloat(data.current_amount)
              parent2 = array_ori.find((y) => y.id_account === parent2.id_account_parent)
              //console.log(parent2)
            }
          }
        })
      }
    })
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "previous_date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          first_start_date: start,
          first_end_date: end
        }
      });
    } 
    else if (name === "previous_date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          first_start_date: null,
          first_end_date: null
        }
      });
    }
    else if (name === "current_date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          second_start_date: start,
          second_end_date: end
        }
      });
    } 
    else if (name === "current_date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          second_start_date: null,
          second_end_date: null
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.first_start_date && filter.first_end_date) {
      string
        ? (string +=
          "&first_start_date=" + filter.first_start_date + "&first_end_date=" + filter.first_end_date)
        : (string +=
          "first_start_date=" + filter.first_start_date + "&first_end_date=" + filter.first_end_date);
    }
    if (filter.second_start_date && filter.second_end_date) {
      string
        ? (string +=
          "&second_start_date=" + filter.second_start_date + "&second_end_date=" + filter.second_end_date)
        : (string +=
          "second_start_date=" + filter.second_start_date + "&second_end_date=" + filter.second_end_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getProfitLossComparativeReports } = this.props;
    //const { current_page } = this.props.profitLossComparativeReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getProfitLossComparativeReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getProfitLossComparativeReports } = this.props;
    this.setState({
      filter: {
        first_start_date: moment().startOf('month').subtract('seconds', 1).startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        first_end_date: moment().startOf('month').subtract('seconds', 1).format("YYYY-MM-DD HH:mm:ss"),
        second_start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        second_end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null
      }
    });
    getProfitLossComparativeReports();
  };

  render() {
    // console.log(this.props.profitLossComparativeReport);
    // Data setup
    const { first_profit_loss_reports, second_profit_loss_reports ,locations, loading, loadingForeign, filter, filter_select, company } =
      this.props.profitLossComparativeReport ? this.props.profitLossComparativeReport : [];
    
    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "40%",
        render: (text, row) => {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          }
          else if (row.level === 1) {
            return text;
          }
          else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }
      },
      {
        title: `${getLanguage({ id: "previousAmount" })} (IDR)`,
        dataIndex: "previous_amount",
        rowKey: "previous_amount",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              !row.account_name.includes("Gross Profit") &&
              !row.account_name.includes("Profit/(Loss)") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: `${getLanguage({ id: "currentAmount" })} (IDR)`,
        dataIndex: "current_amount",
        rowKey: "current_amount",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              !row.account_name.includes("Gross Profit") &&
              !row.account_name.includes("Profit/(Loss)") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: `${getLanguage({ id: "variance" })} (IDR)`,
        dataIndex: "variance",
        rowKey: "variance",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              !row.account_name.includes("Gross Profit") &&
              !row.account_name.includes("Profit/(Loss)") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: getLanguage({ id: "%ofVar" }),
        dataIndex: "percentage",
        rowKey: "percentage",
        align: "right",
        width: "5%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              !row.account_name.includes("Gross Profit") &&
              !row.account_name.includes("Profit/(Loss)") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }

      }
    ]

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let mix_profit_loss = []

    let income_tax_expense = {
      account_name: "Total ",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0
    }

    // console.log(first_profit_loss_reports);

    // previous date

    first_profit_loss_reports.map((data, i) => {

      let previous_amount = parseFloat(data.amount)
      let current_amount  = parseFloat(second_profit_loss_reports[i].amount)
      // let variance        = current_amount - previous_amount

      if (data.id_account !== company.id_account_income_tax) {
        mix_profit_loss.push({
          ...data,
           previous_amount,
           current_amount,
           // variance
        })
        
      }
      else {
        income_tax_expense.account_name += data.account_name
        income_tax_expense.previous_amount = previous_amount
        income_tax_expense.current_amount = current_amount
        //income_tax_expense.variance = variance
      }
    })

    mix_profit_loss.map((data) => {
      if (data.has_child === 1) {
        data.previous_amount = 0
        data.current_amount = 0
      }
    })

    if (mix_profit_loss.length > 0) {
      let level_1 = mix_profit_loss.filter((x) => x.level === 1)
      this.hitungTotalAmountParent(mix_profit_loss, level_1);
    }


    // Baru sampai sini

    let total_income = {}
    let total_cost_of_sales = {};
    let total_expense = {};
    let total_other_income = {};
    let total_other_expense = {};
    let total_gross_profit = {
      //key : "Gross Profit",
      account_name: "Gross Profit",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0
    }
    let total_profit_loss_before_tax = {
      //key : "Profit/(Loss) Before Tax",
      account_name: "Profit/(Loss) Before Tax",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0
    }
    let total_profit_loss_after_tax = {
      //key : "Profit/(Loss) After Tax",
      account_name: "Profit/(Loss) After Tax",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0
    }

    let array_income = [];
    let array_cost_of_sales = [];
    let array_expense = [];
    let array_other_income = [];
    let array_other_expense = [];

    mix_profit_loss.map((data) => {
      //income
      if (data.gl_type === 4) {
        if (data.level === 1) {
          total_income = { ...data, account_name: "Total " + data.account_name }
          total_gross_profit.previous_amount += data.previous_amount
          total_profit_loss_before_tax.previous_amount += data.previous_amount
          total_gross_profit.current_amount += data.current_amount
          total_profit_loss_before_tax.current_amount += data.current_amount
        }
        array_income.push(data);
      }
      else if (data.gl_type === 5) {
        if (data.level === 1) {
          total_cost_of_sales = { ...data, account_name: "Total " + data.account_name }
          total_gross_profit.previous_amount -= data.previous_amount
          total_profit_loss_before_tax.previous_amount -= data.previous_amount
          total_gross_profit.current_amount -= data.current_amount
          total_profit_loss_before_tax.current_amount -= data.current_amount
        }
        array_cost_of_sales.push(data);
      }
      else if (data.gl_type === 6) {
        if (data.level === 1) {
          total_expense = { ...data, account_name: "Total " + data.account_name }
          total_profit_loss_before_tax.previous_amount -= data.previous_amount
          total_profit_loss_before_tax.current_amount -= data.current_amount
        }
        array_expense.push(data);
      }
      else if (data.gl_type === 7) {
        if (data.level === 1) {
          total_other_income = { ...data, account_name: "Total " + data.account_name }
          total_profit_loss_before_tax.previous_amount += data.previous_amount
          total_profit_loss_before_tax.current_amount += data.current_amount
        }
        array_other_income.push(data);
      }
      else if (data.gl_type === 8) {
        if (data.level === 1) {
          total_other_expense = { ...data, account_name: "Total " + data.account_name }
          total_profit_loss_before_tax.previous_amount -= data.previous_amount
          total_profit_loss_before_tax.current_amount -= data.current_amount
        }
        array_other_expense.push(data);
      }
    })

    total_profit_loss_after_tax = {
      ...total_profit_loss_after_tax,
      previous_amount: total_profit_loss_before_tax.previous_amount - income_tax_expense.previous_amount,
      current_amount: total_profit_loss_before_tax.current_amount - income_tax_expense.current_amount
    }

    // total_gross_profit.variance = total_gross_profit.current_amount - total_gross_profit.previous_amount
    // total_profit_loss_before_tax.variance = total_profit_loss_before_tax.current_amount - total_profit_loss_before_tax.previous_amount
    // total_profit_loss_after_tax.variance = total_profit_loss_after_tax.current_amount - total_profit_loss_after_tax.previous_amount
    // Masuk Gan
    let dataSource = []

    if (mix_profit_loss.length > 0) {
      dataSource = [...array_income]
      dataSource.push(total_income)
      dataSource = [...dataSource, ...array_cost_of_sales]
      dataSource.push(total_cost_of_sales);
      dataSource.push(total_gross_profit)

      dataSource = [...dataSource, ...array_expense]
      dataSource.push(total_expense)
      dataSource = [...dataSource, ...array_other_income]
      dataSource.push(total_other_income)
      dataSource = [...dataSource, ...array_other_expense]
      dataSource.push(total_other_expense)
      dataSource.push(total_profit_loss_before_tax)
      dataSource.push(income_tax_expense)
      dataSource.push(total_profit_loss_after_tax)

    }

    // nambah percentage
    dataSource.map((data, i) => {

      let variance = data.current_amount - data.previous_amount;

      dataSource[i] = {
        ...data,
        variance,
        percentage: data.previous_amount ? variance/data.previous_amount*100 : 0
      }
    })

    let dataExcel = [];
    dataSource.map((data) => {
      dataExcel.push({
        ...data,
        account_name: (data.level > 1 ? ' '.repeat(data.level * 3) + data.account_name : data.account_name)
      })
    })

    const fileName = getLanguage({ id: "profitLossComparativeReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="profitLossComparativeReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="profitLossComparativeReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={
                () => (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              scale={0.4}
              title="profitLossComparativeReports.pageHeader"
            />

            <ExcelWrapper
              title="profitLossComparativeReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataExcel}
              filter={filter_select}
              locations={locations}
            />

            <Button style={{ marginLeft: "5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="previousDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.first_start_date &&
                          this.state.filter.first_end_date
                          ? [
                            moment(this.state.filter.first_start_date),
                            moment(this.state.filter.first_end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "previous_date")}
                      disabledDate={e => e.valueOf() > moment(this.state.filter.second_start_date)}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="currentDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.second_start_date &&
                          this.state.filter.second_end_date
                          ? [
                            moment(this.state.filter.second_start_date),
                            moment(this.state.filter.second_end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "current_date")}
                      disabledDate={e => e.valueOf() < moment(this.state.filter.first_end_date)}
                    />
                  </Col>
                  <Col
                    span={12}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {
              !filter
                ? (<Empty description={<IntlMessages id="clickFilter" />} />)
                :
                mix_profit_loss.length === 0 &&
                  !this.props.profitLossComparativeReport.loading ? (
                    <Empty
                      description={
                        <IntlMessages id="profitLossComparativeReports.table.noData" />
                      }
                    />
                  ) : (
                    <div className="isoInvoiceTable">
                      <Divider orientation="left" style={{ fontSize: 14 }}>
                        <IntlMessages id="details" />
                      </Divider>
                      <Scrollbars style={{ width: "100%" }}>
                        <TableWrapper
                          dataSource={dataSource}
                          rowKey={record => record.account_name}
                          columns={columns}
                          loading={loading}
                          className="invoiceListTable"
                          bordered={true}
                          // Set pagination, jika ada page_size mengunakan pagination API
                          // jika tidak ada page_size menggunakan pagination bawaan
                          pagination={false}
                        />
                      </Scrollbars>
                    </div>
                  )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    profitLossComparativeReport: state.profitLossComparativeReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(ProfitLossReport);
